.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.h1 {
  margin: 0px 0px 20px 0px;
  text-align: center;
}

.resultTypeScript,
.resultReact,
.resultEnglish {
  font-size: 35px;
  margin: 0px 0px 15px 0px;
}

.resultTypeScript svg,
.resultReact svg,
.resultEnglish svg {
  margin: 0px 15px 0px 0px;
}

.button {
  margin: 50px 0px 0px 0px;
  cursor: pointer;
  padding: 10px 35px;
  font-size: 25px;
  border-radius: 5px;
  background: linear-gradient(90deg, rgba(76, 175, 80, 1) 0%, rgba(49, 220, 255, 1) 100%);
}

.button:hover {
  color: white;
  transform: scale(1.03);
  transition: all 0.3s ease 0s;
  background: linear-gradient(90deg, rgba(49, 220, 255, 1) 0%, rgba(76, 175, 80, 1) 100%);
}
