.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0 1 468px;
  background-color: #fff8dc;
  border-radius: 25px;
  padding: 10px;
}

.header {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: linear-gradient(90deg, rgba(0, 209, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
  min-height: 100px;
  border-radius: 25px;
  text-align: center;
  color: white;
  font-size: 30px;
}

.header svg {
  margin: 0px 0px 10px 0px;
}

.back {
}

.back:hover {
  color: #c0c0c0;
  transform: scale(1.1);
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.back {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 45px;
  color: white;
}

.programmes {
  margin: 20px 0px 0px 0px;
  width: 100%;
  font-size: 25px;
  text-align: center;
}

.reactBlock,
.typeScriptBlock,
.englishBlock {
  padding: 25px;
  border-radius: 10px;
  cursor: pointer;
  margin: 10px 0px;
}

.reactBlock:hover,
.typeScriptBlock:hover,
.englishBlock:hover {
  transform: scale(1.02);
  transition: all 0.3s ease 0s;
  opacity: 1;
}

.reactBlock {
  opacity: 0.7;
  background: linear-gradient(90deg, rgba(76, 175, 80, 1) 0%, rgba(49, 220, 255, 1) 100%);
}

.typeScriptBlock {
  opacity: 0.7;
  background: linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
}

.englishBlock {
  opacity: 0.7;
  background: linear-gradient(90deg, rgba(0, 0, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
}

.react svg,
.typeScript svg,
.english svg {
  margin: 0px 10px 0px 0px;
  font-size: 35px;
}

.react,
.typeScript,
.english {
  font-size: 35px;
}

.progress {
  border-radius: 10px;
  width: 100%;
  height: 10px;
  background-color: white;
  margin: 15px 0px 0px 0px;
}

.progressReact,
.progressTypeSctipt {
  border-radius: 10px;
  height: 100%;
  background-color: green;
}
