.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0 1 468px;
  background: linear-gradient(90deg, rgba(0, 209, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
  border-radius: 25px;
  max-height: 600px;
  padding: 10px;
}

.progress {
  border-radius: 10px;
  width: 90%;
  height: 15px;
  background-color: white;
  margin: 15px 0px 30px 0px;
}

.progressQuestions {
  transition: all 0.3s ease 0s;
  border-radius: 10px;
  height: 100%;
  background: linear-gradient(90deg, rgba(76, 175, 80, 1) 0%, rgba(49, 220, 255, 1) 100%);
}

.questionsBlock {
  width: 100%;
}

.question {
  font-size: 35px;
  margin: 0px 0px 20px 0px;
}

.answer {
  cursor: pointer;
  font-size: 25px;
  padding: 20px;
  margin: 0px 0px 10px 0px;
  background-color: white;
  border-radius: 10px;
}

.answer:hover,
.button:hover {
  color: white;
  transform: scale(1.03);
  transition: transform 0.3s ease 0s;
  background: linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
}

.button {
  border-radius: 10px;
  font-size: 25px;
  padding: 15px 25px;
  cursor: pointer;
}

.back:hover {
  transform: scale(1.1);
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.back {
  margin: 10px 0px 0px 0px;
  font-size: 45px;
  color: white;
}
