.modalOkno {
  display: flex;
  flex-direction: column;
}
.input,
.button {
  padding: 15px;
  font-size: 22px;
  border-radius: 5px;
  border: 2px solid black;
}
.input {
  margin: 0px 0px 5px 0px;
}
.button {
  width: 100%;
  position: relative;
  top: 0;
  cursor: pointer;
  color: white;
  background: linear-gradient(90deg, rgba(255, 255, 0, 1) 0%, rgba(255, 59, 0, 1) 100%);
}
.button:hover {
  transition: all 0.3s ease 0s;
  top: 2px;
  box-shadow: 0 3px 0px #3c354a;
}
